body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    position: relative;
}

.content {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 200px;
}
.sidebar {
    position: fixed;
    width: 200px;
    height: 100vh;
    top: 0;
    left: 0;
    bottom: 0;
    background-color: #666;
    z-index: 100;
    padding: 10px;
    box-shadow: inset -1px 0 0 rgba(0, 0, 0, .1);
    overflow-y: auto;
}